import React from 'react'
import { Link } from 'react-router-dom'

export default function Navbar({ LoginUser, logout }) {
    return (
        <nav className="navbar navbar-expand-lg bg-transparent ">
            <div className="container-fluid">
                <Link className="navbar-brand" to="/movie-rev/home">View</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">

                    {LoginUser ? <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link " aria-current="page" to="/movie-rev/home">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link " aria-current="page" to="/movie-rev/movie">Movie</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link " aria-current="page" to="/movie-rev/tv">Tv Show</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link " aria-current="page" to="/movie-rev/People">People</Link>
                        </li>
                        


                    </ul> : ''}


                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        {LoginUser ?<form className="d-flex" role="search">
                            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        </form> :""}
                        
                        <li className="nav-item d-flex">
                            <a className="nav-link me-2" aria-current="page" href="https://www.facebook.com/profile.php?id=100007670174877"  target='_blank'><i class="fa-brands fa-facebook"></i></a>
                            <a className="nav-link me-2" aria-current="page" href="https://www.instagram.com/mohad.jaber/"  target='_blank'><i class="fa-brands fa-instagram"></i></a>
                            <a className="nav-link me-2" aria-current="page" href="https://www.linkedin.com/in/mohammad-jaber-85238821b" target='_blank'><i class="fa-brands fa-linkedin"></i></a>
                        </li>

                        {LoginUser ? <li className="nav-item">
                            <button onClick={logout} className="bg-transparent border-0 text-white p-2" >Logout</button>
                        </li> : <li className="nav-item">
                            <Link className="nav-link " aria-current="page" to="/movie-rev/Login">Login</Link>
                        </li>}






                    </ul>

                </div>
            </div>
        </nav>

    )
}
