import React, { useEffect } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import axios from 'axios';
import { useState } from 'react';
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css"
import avatar from "../../img/download.jpg";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";
export default function PersonDetails() {
    let [details, setDetails] = useState([]);

    let [serachParams, setSerachParams] = useSearchParams();
    let curruntId = serachParams.get(`id`);
    let imgPath = "https://image.tmdb.org/t/p/w500";
    let [loading, setLoading] = useState(false);


    let navigat = useNavigate();


    async function getDetail() {
        let { data } = await axios.get(`https://api.themoviedb.org/3/person/${curruntId}?api_key=ad2e4f85e87dc071d546d2930fef7a1a&language=en-US`)
        setDetails(data);
        setLoading(true);
    }


    useEffect(() => {
        getDetail();
    }, [])

    return (
        <>
            {localStorage.getItem("Token") ?

                <div>
                    {loading ?
                        <div>
                            <div className='row mt-4'>
                                <div className='col-lg-4'>
                                    <img className='w-100' src={details.profile_path ? imgPath + details.profile_path : avatar} alt="av" />
                                </div>
                                <div className='col-lg-7'>
                                    <h1>{details.name}</h1>
                                    <h4 className='p-2 mt-3'>birthday : {details.birthday} </h4>
                                    <h4 className='p-2 mt-3'>deathday : {details.deathday ? details.deathday : 'Alive'} </h4>
                                    <h4 className='p-2 mt-3'>gender  : {details.gender == 1 ? "female" : "male"}  </h4>
                                    <h4 className='p-2 mt-3'>known for department :{details.known_for_department}   </h4>
                                    <h4 className='p-2 mt-3'>place of birth: {details.place_of_birth} </h4>
                                    <h4 className='p-2 mt-3'>popularity: {details.popularity} </h4>
                                </div>
                            </div>
                        </div>
                        :
                        <div className='vh-100 d-flex justify-content-center align-items-center'>
                            <div class="spinner-border" role="status">
                                <span class="visually-hidden">Loading...</span>
                            </div>

                        </div>
                    }
                </div>




                : navigat('/movie-rev/reception')}

        </>
    )
}
