import React from 'react'
import Navbar from './../Navbar/Navbar'
import Home from './../Home/Home';
import Movie from './../Movie/Movie';
import Tv from './../Tv/Tv';
import People from './../People/People';


import Register from './../Register/Register';
import Login from './../Login/Login';
import Notfound from './../Notfound/Notfound';
import {Routes ,Route} from 'react-router-dom';
import { useNavigate, Link } from 'react-router-dom';

import { useState, useEffect } from 'react';
import jwtDecode from 'jwt-decode';
import Reception from './../reception/Reception';
import "../../index.css"
import MovieDetails from '../Movie/MovieDetails';
import TvDetails from './../Tv/TvDetails';
import PersonDetails from './../People/PersonDetails';

export default function App() {

  let[LoginUser,setLoginUser]= useState();
  let navigat = useNavigate();
  function logout(){
    localStorage.removeItem("Token");
    setLoginUser(null);
    navigat('/movie-rev/reception')
  }

  function getUserLogin(){
    let token=localStorage.getItem('Token');
    let decode = jwtDecode(token);
    setLoginUser(decode);
    // console.log(LoginUser);
  }

  useEffect(()=>{
    if(localStorage.getItem("Token")){
      getUserLogin();
    }else{
      navigat('/movie-rev/reception')
    }
  },[])
  return (
    <div>
      <Navbar LoginUser={LoginUser} logout={logout}/>
      
      <div className="container">
          <Routes>
            <Route path='/' element={<Home/>}></Route>
            <Route path='/movie-rev/' element={<Home/>}></Route>
            <Route path='/movie-rev/home' element={<Home/>}></Route>
            <Route path='/movie-rev/Movie' element={<Movie/>}></Route>
            <Route path='/movie-rev/MovieDetails' element={<MovieDetails/>} ></Route>
            <Route path='/movie-rev/TvDetails' element={<TvDetails/>} ></Route>
            <Route path='/movie-rev/PersonDetails' element={<PersonDetails/>} ></Route>
            <Route path='/movie-rev/Tv' element={<Tv/>}></Route>
            <Route path='/movie-rev/People' element={<People/>}></Route>
            <Route path='/movie-rev/Login' element={<Login getUserLogin={getUserLogin}/>}></Route>
            <Route path='/movie-rev/Register' element={<Register/>}></Route>
            <Route path='*' element={<Notfound/>}></Route> 
            <Route path='/movie-rev/reception' ></Route>  
          </Routes>
          </div>
      
          <Routes>
          <Route path='/movie-rev/reception' element={<Reception/>}></Route>  
          </Routes>

    </div>
  )
}

