import React from 'react'
import axios from 'axios';
import { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";
import { useEffect } from 'react';

import "../../index.css";
import { useNavigate } from 'react-router-dom';



export default function Movie() {


  let [anime, setAnime] = useState([]);
  let [action, setAction] = useState([]);
  let [Comedy, setComedy] = useState([]);
  let [drama, setDrama] = useState([]);
  let [family, setFamily] = useState([]);
  let [war, setWar] = useState([]);
  let [Documentary, setDocumentary] = useState([]);
  let [loading, setLoading] = useState(false);
  
  let imgPath = "https://image.tmdb.org/t/p/w500";
  let navigat = useNavigate();


  async function getMovie(type, callback) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=ad2e4f85e87dc071d546d2930fef7a1a&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=1&with_genres=${type}&with_watch_monetization_types=flatrate`);
    callback(data.results);


  }

  useEffect(() => {
    api()
  }, [])

  async function api() {
    await getMovie('16', setAnime);
    await getMovie('28', setAction);
    await getMovie('35', setComedy);
    await getMovie('10749', setDrama);
    await getMovie('10751', setFamily);
    await getMovie('10752', setWar);
    await getMovie('99', setDocumentary);
    setLoading(true);
  }



  function goToDetail(id) {
    navigat({
      pathname: `/movie-rev/MovieDetails`,
      search: `?id=${id}`
    })
  }


  return (
    <>
      {localStorage.getItem("Token") ?


        <div>
          {loading ? <>
            <div>

              <div className=''>
                <div>

                  <h2>Animation  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {anime.map((s, index) =>
                  <SwiperSlide key={index} >
                    <div onClick={() => goToDetail(s.id)} className='position-relative'>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                      {/* <span class="position-absolute top-50 start-100 translate-middle p-2 bg-danger border border-light rounded-circle">
          <span class="visually">10</span>
        </span> */}
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Action  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {action.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Comedy  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {Comedy.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>



            <div>

              <div className=''>
                <div>

                  <h2>Drama  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {drama.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Family  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {family.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>

            <div>

              <div className=''>
                <div>

                  <h2>War  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {war.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Documentary  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {Documentary.map((s, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToDetail(s.id)}>
                      <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{s.original_title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>
          </>
            :
            <div className='vh-100 d-flex justify-content-center align-items-center'>
              <div class="spinner-border" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>

            </div>
          }

        </div>





        :
        navigat('/movie-rev/reception')
      }
    </>



  )
}
