import React from 'react'
import axios from 'axios';
import { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";
import { useEffect } from 'react';
import Movie from './../Movie/Movie';
import "../../index.css";
import avatar from "../../img/download.jpg";
import { async } from '@firebase/util';
import { useNavigate } from 'react-router-dom';



export default function Home() {
  let imgPath = "https://image.tmdb.org/t/p/w500";
  let [trendingMovie, setTrendingMovie] = useState([]);
  let [trendingTV, setTrendingTv] = useState([]);
  let [trendingPeople, setTrendingPeople] = useState([]);
  let [loading, setLoading] = useState(false);
  let navigat = useNavigate();


  async function getTrending(type, callback) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${type}/day?api_key=ad2e4f85e87dc071d546d2930fef7a1a`);
    callback(data.results);
  }

  useEffect(() => {

    api()
    

  }, [])

  async function api() {
    await getTrending('movie', setTrendingMovie);
    await getTrending('tv', setTrendingTv);
    await getTrending('person', setTrendingPeople);
    setLoading(true);
  }

  function goToMovieDetail(id) {
    navigat({
      pathname: `/movie-rev/MovieDetails`,
      search: `?id=${id}`
    })
  }

  function goToTvDetail(id) {
    navigat({
      pathname: `/movie-rev/TvDetails`,
      search: `?id=${id}`
    })
  }

  function goToPersonDetail(id) {
    navigat({
      pathname: `/movie-rev/PersonDetails`,
      search: `?id=${id}`
    })
  }

  return (



    <>
      {localStorage.getItem("Token") ?

        <div>
          {loading ? <>
            <div>

              <div className=''>
                <div>

                  <h2>Trending  Movies </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 10,

                  },
                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {trendingMovie.map((movie, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToMovieDetail(movie.id)}>
                      <img className='w-100' src={imgPath + movie.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{movie.title}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Trending Tv</h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 20,
                  },

                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {trendingTV.map((tv, index) =>
                  <SwiperSlide key={index}>
                    <div onClick={() => goToTvDetail(tv.id)}>
                      <img className='w-100' src={imgPath + tv.poster_path} alt="moive img" />
                      <h3 className='fs-6'>{tv.name}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>


            <div>

              <div className=''>
                <div>

                  <h2>Trending Person </h2>
                </div>
              </div>


              <Swiper
                slidesPerView={6}
                grid={{
                  rows: 1,
                }}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  320: {
                    slidesPerView: 2,
                    spaceBetween: 20,
                  },

                  768: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 6,
                    spaceBetween: 20,
                  },
                }}
                navigation={true}
                mousewheel={true}

                modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                className="mySwiper pb-3"
              >

                {trendingPeople.map((people, index) =>

                  <SwiperSlide key={index}>
                    <div onClick={() => goToPersonDetail(people.id)}>
                      <img className='w-100 h-100' src={people.profile_path ? imgPath + people.profile_path : avatar} alt="pesron img" />
                      <h3 className='fs-6'>{people.name}</h3>
                    </div>
                  </SwiperSlide>
                )}
              </Swiper>

            </div>
          </>
            :
            <div className='vh-100 d-flex justify-content-center align-items-center'>
              <div class="spinner-border" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>

            </div>
          }

        </div>
        :
        navigat('/movie-rev/reception')
      }

    </>







  )
}
