import React, { useState } from 'react'
import Joi from 'joi';
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import app from './../../firebase';
import { useNavigate, Link } from 'react-router-dom';

export default function Login({ getUserLogin }) {

  let [user, setUser] = useState({
    email: '',
    password: ''
  })

  let [errorList, setErrorList] = useState([]);
  let [looding, setlooding] = useState(false);



  function submitFormData(e) {
    e.preventDefault();
    setlooding(true);
    let validateResult = valideteForm();
    if (validateResult.error) {
      setErrorList(validateResult.error.details);
      setlooding(false);
    } else {
      signIn();
      setlooding(false);

    }
  }

  function getFormValue(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);


  }

  function valideteForm() {
    const schema = Joi.object({
      email: Joi.string().required().email({ tlds: { allow: ['com', 'net'] } }),
      password: Joi.string().required(),
    });

    return schema.validate(user, { abortEarly: false });
  }

  const auth = getAuth(app);


  let signIn = () => {
    signInWithEmailAndPassword(auth, user.email, user.password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        // console.log(user);
        localStorage.setItem("Token", user.accessToken);
        getUserLogin();
        goToHome();
        // ...
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = [error];
        setErrorList(errorMessage);

      });
  }


  let Navigate = useNavigate();
  function goToHome() {
    let path = "/movie-rev/home";
    Navigate(path);
  }
  function gotToRegister() {
    let path = "/movie-rev/Register";
    Navigate(path);
  }

  return (
    <div>
      <h2 className='my-5'>Login form</h2>
      {errorList.map((error, index) => <div key={index} className="alert alert-danger">
        {error.message}
      </div>)}

      <form onSubmit={submitFormData}>

        <div className='mb-3'>
          <label htmlFor='email' className='form-lable'>email</label>
          <input onChange={getFormValue} type="email" name='email' className='form-control' id='email' />
        </div>

        <div className='mb-3'>
          <label htmlFor='password' className='form-lable'>Password</label>
          <input onChange={getFormValue} type="password" name='password' className='form-control' id='password' />
        </div>
        <button className='btn btn-info' >
          {looding ? <i className='fa fa-spinner fa-spin'></i> : "Login"}
        </button>

      </form>
      <h3>Don't have an account ?<button onClick={gotToRegister} className="bg-transparent border-0 text-danger">Register</button>
      </h3>
    </div>
  )
}
