import React from 'react'
import recimg from '../../img/537552.jpg'
import style from '../reception/reception.module.css'
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css"
import { Link } from 'react-router-dom'

export default function Reception() {
    return (
        <div className={`${style.recp} w-100  row position-relative`}>
            <div className={`${style.text} col-lg-4 col-md-6 col-11`}>
                <h1>Let's Make Your <br /> Own Cinema</h1>
                <p className='pt-3'>You can still enjoy the latest movies and other movies online and at free</p>
                <div className='row   ' >
                    <Link className="me-4 col-4 btn btn-outline-danger " to="/movie-rev/Register">Register</Link>
                    <Link className="col-4 btn btn-danger" to="/movie-rev/login">Login</Link>



                </div>
            </div>
        </div>
    )
}
