import React, { useState } from 'react'
import Joi from 'joi';
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import app from './../../firebase';
import { useNavigate } from 'react-router-dom';


export default function Register() {

  let [user, setUser] = useState({
    first_name: '',
    last_name: '',
    age: '',
    email: '',
    password: ''
  })

  let [errorList, setErrorList] = useState([]);
  let [looding , setlooding] =useState(false);

  let Navigate = useNavigate();
  function goToLogin() {
    let path = "/movie-rev/login";
    Navigate(path);
  }


  function submitFormData(e) {
    e.preventDefault();
    setlooding(true);
    let validateResult = valideteForm();
    if (validateResult.error) {
      setErrorList(validateResult.error.details);
      setlooding(false);
    } else {
      Register();
      setlooding(false);

    }
  }

  function getFormValue(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);


  }

  function valideteForm() {
    const schema = Joi.object({
      first_name: Joi.string().required().min(3).max(10),
      last_name: Joi.string().required().min(3).max(10),
      age: Joi.number().required().min(20).max(80),
      email: Joi.string().required().email({ tlds: { allow: ['com', 'net'] } }),
      password: Joi.string().required().pattern(new RegExp('^[A-Z][0-9]{6,}$')),
    });

    return schema.validate(user, { abortEarly: false });
  }




  const auth = getAuth(app);

  let Register = () => {
    createUserWithEmailAndPassword(auth, user.email, user.password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        goToLogin();
        // ...
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = [error];
        setErrorList(errorMessage);

        // ..
      });

  }

  return (
    <div>
      <h2 className='my-5'>Register form</h2>
      {errorList.map((error, index) => <div key={index} className="alert alert-danger">
        {error.message}
      </div>)}

      <form onSubmit={submitFormData}>
        <div className='mb-3'>
          <label htmlFor='first-name' className='form-lable'>First Name</label>
          <input onChange={getFormValue} type="text" name='first_name' className='form-control' id='first-name' />
        </div>

        <div className='mb-3'>
          <label htmlFor='last-name' className='form-lable'>Last Name</label>
          <input onChange={getFormValue} type="text" name='last_name' className='form-control' id='last-name' />
        </div>

        <div className='mb-3'>
          <label htmlFor='age' className='form-lable'>Age</label>
          <input onChange={getFormValue} type="number" name='age' className='form-control' id='age' />
        </div>

        <div className='mb-3'>
          <label htmlFor='email' className='form-lable'>email</label>
          <input onChange={getFormValue} type="email" name='email' className='form-control' id='email' />
        </div>

        <div className='mb-3'>
          <label htmlFor='password' className='form-lable'>Password</label>
          <input onChange={getFormValue} type="password" name='password' className='form-control' id='password' />
          <p className='text-danger'>(Password must be first character capital letter and the other number )</p>
        </div>
        <button className='btn btn-info' >
          
          {looding ? <i className='fa fa-spinner fa-spin'></i> : "Register"}
          
          </button>

      </form>
    </div>
  )
}
