import React from 'react'
import axios from 'axios';
import { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";
import { useEffect } from 'react';

import "../../index.css";
import { useNavigate } from 'react-router-dom';




export default function Tv() {


  let [Comedy, setComedy] = useState([]);
  let [drama, setDrama] = useState([]);
  let [family, setFamily] = useState([]);
  let [Crime, setCrime] = useState([]);
  let [Documentary, setDocumentary] = useState([]);
  let imgPath = "https://image.tmdb.org/t/p/w500";
  let navigat = useNavigate();
  let [loading, setLoading] = useState(false);




  async function getTv(type, callback) {
    let { data } = await axios.get(`
    https://api.themoviedb.org/3/discover/tv?api_key=ad2e4f85e87dc071d546d2930fef7a1a&sort_by=popularity.desc&page=1&with_genres=${type}&include_null_first_air_dates=false&with_watch_monetization_types=free&with_status=0&with_type=0`);
    callback(data.results);

  }

  useEffect(() => {

    api()

  }, [])

  async function api() {
    await getTv('35', setComedy);
    await getTv('18', setDrama);
    await getTv('10751', setFamily);
    await getTv('80', setCrime);
    await getTv('99', setDocumentary);

    setLoading(true);
  }

  function goToTvDetail(id) {
    navigat({
      pathname: `/movie-rev/TvDetails`,
      search: `?id=${id}`
    })
  }


  return (
    <>
      {localStorage.getItem("Token") ?
        <div>
          {loading ?
            <div>
              <div>

                <div className=''>
                  <div>

                    <h2>Comedy  Tvs </h2>
                  </div>
                </div>


                <Swiper
                  slidesPerView={6}
                  grid={{
                    rows: 1,
                  }}
                  spaceBetween={10}
                  pagination={{
                    clickable: true,
                  }}
                  breakpoints={{
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 10,

                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 40,
                    },
                    1024: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                  }}
                  navigation={true}
                  mousewheel={true}

                  modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                  className="mySwiper pb-3"
                >

                  {Comedy.map((s, index) =>
                    <SwiperSlide key={index}>
                      <div onClick={() => goToTvDetail(s.id)}>
                        <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                        <h3 className='fs-6'>{s.original_name}</h3>
                      </div>
                    </SwiperSlide>
                  )}
                </Swiper>

              </div>

              <div>

                <div className=''>
                  <div>

                    <h2>Drama  Tvs </h2>
                  </div>
                </div>


                <Swiper
                  slidesPerView={6}
                  grid={{
                    rows: 1,
                  }}
                  spaceBetween={10}
                  pagination={{
                    clickable: true,
                  }}
                  breakpoints={{
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 10,

                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 40,
                    },
                    1024: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                  }}
                  navigation={true}
                  mousewheel={true}

                  modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                  className="mySwiper pb-3"
                >

                  {drama.map((s, index) =>
                    <SwiperSlide key={index}>
                      <div onClick={() => goToTvDetail(s.id)}>
                        <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                        <h3 className='fs-6'>{s.original_name}</h3>
                      </div>
                    </SwiperSlide>
                  )}
                </Swiper>

              </div>

              <div>

                <div className=''>
                  <div>

                    <h2>Family  Tvs </h2>
                  </div>
                </div>


                <Swiper
                  slidesPerView={6}
                  grid={{
                    rows: 1,
                  }}
                  spaceBetween={10}
                  pagination={{
                    clickable: true,
                  }}
                  breakpoints={{
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 10,

                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 40,
                    },
                    1024: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                  }}
                  navigation={true}
                  mousewheel={true}

                  modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                  className="mySwiper pb-3"
                >

                  {family.map((s, index) =>
                    <SwiperSlide key={index}>
                      <div onClick={() => goToTvDetail(s.id)}>
                        <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                        <h3 className='fs-6'>{s.original_name}</h3>
                      </div>
                    </SwiperSlide>
                  )}
                </Swiper>

              </div>

              <div>

                <div className=''>
                  <div>

                    <h2>Crime  Tvs </h2>
                  </div>
                </div>


                <Swiper
                  slidesPerView={6}
                  grid={{
                    rows: 1,
                  }}
                  spaceBetween={10}
                  pagination={{
                    clickable: true,
                  }}
                  breakpoints={{
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 10,

                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 40,
                    },
                    1024: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                  }}
                  navigation={true}
                  mousewheel={true}

                  modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                  className="mySwiper pb-3"
                >

                  {Crime.map((s, index) =>
                    <SwiperSlide key={index}>
                      <div onClick={() => goToTvDetail(s.id)}>
                        <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                        <h3 className='fs-6'>{s.original_name}</h3>
                      </div>
                    </SwiperSlide>
                  )}
                </Swiper>

              </div>

              <div>

                <div className=''>
                  <div>

                    <h2>Documentary  Tvs </h2>
                  </div>
                </div>


                <Swiper
                  slidesPerView={6}
                  grid={{
                    rows: 1,
                  }}
                  spaceBetween={10}
                  pagination={{
                    clickable: true,
                  }}
                  breakpoints={{
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 10,

                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 40,
                    },
                    1024: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                  }}
                  navigation={true}
                  mousewheel={true}

                  modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                  className="mySwiper pb-3"
                >

                  {Documentary.map((s, index) =>
                    <SwiperSlide key={index}>
                      <div onClick={() => goToTvDetail(s.id)}>
                        <img className='w-100' src={imgPath + s.poster_path} alt="moive img" />
                        <h3 className='fs-6'>{s.original_name}</h3>
                      </div>
                    </SwiperSlide>
                  )}
                </Swiper>

              </div>
            </div>
            :
            <div className='vh-100 d-flex justify-content-center align-items-center'>
              <div class="spinner-border" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>

            </div>
          }
        </div>

        :
        navigat('/movie-rev/reception')
      }
    </>






  )
}
