import React, { useEffect } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import axios from 'axios';
import { useState } from 'react';
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css"

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";


export default function TvDetails() {

    let [details, setDetails] = useState([]);
    let [geners, setGeners] = useState([]);
    let [seasons, setSeasons] = useState([]);
    let [serachParams, setSerachParams] = useSearchParams();
    let curruntId = serachParams.get(`id`);
    let imgPath = "https://image.tmdb.org/t/p/w500";
    let navigat = useNavigate();
    let [loading, setLoading] = useState(false);


    async function getDetail() {
        let { data } = await axios.get(`https://api.themoviedb.org/3/tv/${curruntId}?api_key=ad2e4f85e87dc071d546d2930fef7a1a&language=en-US`)
        setDetails(data);
        setGeners(data.genres)
        setSeasons(data.seasons);
        setLoading(true);
    }

    useEffect(() => {
        getDetail();
    }, [])
    return (
        <>
            {localStorage.getItem("Token") ?
                <div>
                    {loading ?
                        <div>

                            <div className='row mt-4'>
                                <div className='col-lg-4'>
                                    <img className='w-100' src={imgPath + details.poster_path} alt="av" />

                                </div>
                                <div className='col-lg-7'>
                                    <h1>{details.name}</h1>
                                    <h3 className='sec-color'>{details.tagline}</h3>
                                    <div className="row p-2">
                                        {geners.map((g, index) =>
                                            <span class=" col-lg-2 col-3   me-3 badge text-bg-primary">{g.name}</span>
                                        )}
                                    </div>
                                    <h4 className='p-2 mt-3'>Vote : {details.vote_average} </h4>
                                    <h4 className='p-2 mt-3'>Original language : {details.original_language} </h4>
                                    <h4 className='p-2 mt-3'>first air date  : {details.first_air_date}  </h4>
                                    <h4 className='p-2 mt-3'>last air date :{details.last_air_date}   </h4>
                                    <h4 className='p-2 mt-3'>status: {details.status} </h4>
                                    <h4 className='p-2 mt-3'>number of seasons: {details.number_of_seasons} </h4>
                                    <h4 className='p-2 mt-3'>episode: {details.episode_run_time} </h4>
                                    <h4 className='p-2 mt-3 sec-color'>{details.overview} </h4>
                                </div>
                            </div>



                            <div>

                                <div className=''>
                                    <div>

                                        <h2>Seasons </h2>
                                    </div>
                                </div>


                                <Swiper
                                    slidesPerView={4}
                                    grid={{
                                        rows: 1,
                                    }}
                                    spaceBetween={10}
                                    pagination={{
                                        clickable: true,
                                    }}
                                    breakpoints={{
                                        320: {
                                            slidesPerView: 2,
                                            spaceBetween: 10,

                                        },
                                        768: {
                                            slidesPerView: 4,
                                            spaceBetween: 40,
                                        },
                                        1024: {
                                            slidesPerView: 6,
                                            spaceBetween: 20,
                                        },
                                    }}
                                    navigation={true}
                                    mousewheel={true}

                                    modules={[Grid, Pagination, Mousewheel, Navigation, Keyboard]}
                                    className="mySwiper pb-3"
                                >

                                    {seasons.map((s, index) =>
                                        <SwiperSlide key={index}>
                                            <div>
                                                <img className='w-100 h-100' src={s.poster_path ? imgPath + s.poster_path : imgPath + details.poster_path} alt="Tv img" />
                                                <h3 className='fs-6'>{s.name}</h3>
                                            </div>
                                        </SwiperSlide>
                                    )}
                                </Swiper>

                            </div>
                        </div>
                        :
                        <div className='vh-100 d-flex justify-content-center align-items-center'>
                            <div class="spinner-border" role="status">
                                <span class="visually-hidden">Loading...</span>
                            </div>

                        </div>
                    }
                </div>

                : navigat('/movie-rev/reception')}
        </>
    )
}
