import React from 'react'
import axios from 'axios';
import { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/pagination";
// import required modules
import { Grid, Navigation, Pagination, Mousewheel, Keyboard } from "swiper";
import { useEffect } from 'react';

import "../../index.css";
import { useNavigate } from 'react-router-dom';
import avatar from "../../img/download.jpg";


export default function People() {

  let [people, setPeople] = useState([]);
  let navigat = useNavigate();
  let imgPath = "https://image.tmdb.org/t/p/w500";
  let [loading, setLoading] = useState(false);

  async function getPeople() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=ad2e4f85e87dc071d546d2930fef7a1a&language=en-US&page=1`);
    setPeople(data.results);
    setLoading(true);
  }

  useEffect(() => {
    getPeople();
  }, [])

  function goToPersonDetail(id) {
    navigat({
      pathname: `/movie-rev/PersonDetails`,
      search: `?id=${id}`
    })
  }

  return (
    <>
      {localStorage.getItem("Token") ?

        <div>
          {loading ?
            <div>

              <div className=''>
                <div>
                  <h2>Popular Person </h2>
                </div>
              </div>
              <div className='row'>
                {people.map((people, index) =>
                  <div className='col-lg-3 col-md-6 p-5 ' onClick={() => goToPersonDetail(people.id)} key={index}>
                    <img className='w-100 h-100' src={people.profile_path ? imgPath + people.profile_path : avatar} alt="pesron img" />
                    <h3 className='fs-6'>{people.name}</h3>
                  </div>
                )}

              </div>
            </div>
            :
            <div className='vh-100 d-flex justify-content-center align-items-center'>
              <div class="spinner-border" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>

            </div>}
        </div>



        : navigat('/movie-rev/reception')}
    </>
  )
}
